"use strict";

var textFrom = document.getElementById("value-from");
var textTo = document.getElementById("value-to");
$("#price-slider").ionRangeSlider({
  skin: "round",
  type: "double",
  min: "80",
  max: "120",
  step: "2",
  hide_min_max: "true",
  hide_from_to: "true",
  onStart: function onStart(data) {
    var valueFrom = data.from;
    var valueTo = data.to;
    textFrom.innerText = valueFrom + ",00 z";
    textTo.innerText = valueTo + ",00 z";
  },
  onChange: function onChange(data) {
    var valueFrom = data.from;
    var valueTo = data.to;
    textFrom.innerText = valueFrom + ",00 z";
    textTo.innerText = valueTo + ",00 z";
  },
  onFinish: function onFinish(data) {
    var valueFrom = data.from;
    var valueTo = data.to;
    productsItems.forEach(function (item) {
      if (valueFrom > item.getAttribute("data-price") || valueTo < item.getAttribute("data-price")) {
        item.classList.add("hide");
      } else {
        item.classList.remove("hide");
      }
    });
    textFrom.innerText = valueFrom + ",00 z";
    textTo.innerText = valueTo + ",00 z";
  }
});